<template>
    <el-dialog class="add-case-dialog" :title="title" :visible.sync="dialogVisible" @close="resetForm">
        <el-form ref="dialogForm" :model="dialogForm" :rules="orgRules" label-width="100px">
            <el-row type="flex" :gutter="15">
                <el-col :span="16">
                    <el-form-item label="案件名称" prop="name">
                        <el-input v-model="dialogForm.name" placeholder="请填写案件名称"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">

                </el-col>
            </el-row>
            <el-row type="flex" :gutter="15">
              <el-col :span="8">
                <el-form-item label="发生日期" prop="happenDate">
                    <el-date-picker v-model="dialogForm.happenDate" type="date" placeholder="请选择日期"></el-date-picker>
                </el-form-item>  
              </el-col>
              <el-col :span="8">
                <el-form-item label="案件分级" prop="rank">
                    <el-select placeholder="请选择案件分级" clearable v-model="dialogForm.rank">
                        <el-option v-for="(item, index) in dic['案件分级']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="案件类型" prop="type">
                    <el-select placeholder="请选择案件类型" clearable v-model="dialogForm.type">
                        <el-option v-for="(item, index) in dic['案件类型']" :label="item.name" :value="item.code" :key="index"></el-option>
                    </el-select>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row type="flex" :gutter="15">
              <el-col :span="8">
                <!-- todo 需要改成标准省市菜单选择 -->
                <el-form-item label="发生地" prop="happenAddress">
                    <el-select placeholder="请选择居住地" clearable v-model="dialogForm.happenAddress">
                        <el-option v-for="(item, index) in dic['行政区划']" :label="item.name" :value="item.code" :key="index"></el-option>                        
                    </el-select>
                </el-form-item>  
              </el-col>
              <el-col :span="16">
                <el-form-item label="发生地详址" prop="happenAddressDetail">
                    <el-input v-model="dialogForm.happenAddressDetail" placeholder="请填写发生地详址"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row type="flex" :gutter="15">
              <el-col :span="24">
                <el-form-item label="案件情况" prop="status">
                    <el-input v-model="dialogForm.status" type="textarea" :rows="5" placeholder="请填写案件情况"></el-input>
                </el-form-item>  
              </el-col>
            </el-row>
        </el-form>
        <techo-position ref="position" :code="dialogForm.districtCode" :placement="'right'" @formLonLat="getLonlat"></techo-position>
        <div class="lon-lat">
            <span>经度：</span>
            <span>{{ dialogForm.x }}</span>
            <span>经度：</span>
            <span>{{ dialogForm.y }}</span>
        </div>
        <div slot="footer" class="dialog-footer">
			<el-button type="warning" size="small" @click="resetForm">取消</el-button>
            <el-button type="success" size="small" @click="submitForm">保存</el-button>
		</div>
    </el-dialog>
</template>
<script>
import service from "./services/zz_service.js";
export default {
  data() {
    return {
      dialogVisible: false,
      isAdd: true,
      title: "",
      dic: {
        案件分级: [],
        案件类型: [],
        行政区划: []
      },
      dialogForm: {
        x: "",
        y: "",
        districtCode: "",
        happenAddress: "610000",
        happenAddressDetail: ""
      },
      orgRules: {
        name: [
          {
            required: true,
            message: "请输入案（事）件名称",
            trigger: "blur"
          },
          {
            min: 2,
            max: 25,
            message: "长度在 2 到 25 个字符",
            trigger: "blur"
          }
        ],
        happenDate: [
          {
            required: true,
            message: "请选择发生日期",
            trigger: "change"
          }
        ],
        happenAddress: [
          {
            required: true,
            message: "请选择所在地",
            trigger: "change"
          }
        ],
        happenAddressDetail: [
          {
            required: true,
            message: "请输入发生地详址",
            trigger: "blur"
          },
          {
            min: 2,
            max: 25,
            message: "长度在 2 到 25 个字符",
            trigger: "blur"
          }
        ],
        rank: [
          {
            required: true,
            message: "请选择案件分级",
            trigger: "change"
          }
        ],
        type: [
          {
            required: true,
            message: "请选择案件类型",
            trigger: "change"
          }
        ],
        status: [
          {
            required: true,
            message: "请输入被实施地区",
            trigger: "blur"
          },
          {
            min: 2,
            max: 4000,
            message: "长度在 2 到 4000 个字符",
            trigger: "blur"
          }
        ]
      }
    };
  },

  created() {
    let that = this;
    /** 获取字典表目录 */
    service.requestDicCatalog(data => {
      let dicKeys = _.keys(that.dic);
      _.map(data, item => {
        if (_.indexOf(dicKeys, item.name) != -1) {
          /** 通过字典表目录ID查询字典表 */
          service.requestDicType(item.id, data => {
            that.dic[item.name] = data;
          });
        }
      });
    });
  },

  watch: {
    "dialogForm.districtCode": {
      handler(code) {
        if (code) {
          service.requestCodeByAddress(code, data => {
            if (data.belongname) {
              this.dialogForm.happenAddressDetail = data.belongname + data.name;
            } else {
              this.dialogForm.happenAddressDetail = "铜川市";
            }
          });
        }
      }
    },
    isAdd(value) {
      if (value == false) {
        this.$nextTick(() => {
          this.$refs["position"].addMarker({
            lng: this.dialogForm.x,
            lat: this.dialogForm.y
          });
        });
      }
    }
  },

  methods: {
    getLonlat(value) {
      this.dialogForm.x = value.lng;
      this.dialogForm.y = value.lat;
    },
    /** 保存 */
    submitForm() {
      if (this.dialogForm.x == undefined || this.dialogForm.x == "") {
        this.$popup.showNotification({
          type: "warning",
          message: "请选择地理位置！"
        });
      } else {
        this.$refs["dialogForm"].validate(valid => {
          if (valid) {
            let param = _.cloneDeep(this.dialogForm);
            param.dicMap && delete param.dicMap;
            param.happenDate = util.time.formatDate(
              param.happenDate,
              "YYYY-MM-DD"
            );
            if (this.isAdd == true) {
              // 添加
              service.requestSaveCase(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "重大案（事）件添加成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            } else if (this.isAdd == false) {
              // 修改
              service.requestUpdateCase(param, data => {
                this.$popup.showNotification({
                  type: "success",
                  message: "重大案（事）件修改成功！"
                });
                this.resetForm();
                this.$parent.handleQuery();
              });
            }
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    /** 取消 */
    resetForm() {
      this.$refs["dialogForm"].resetFields();
      this.$data.dialogVisible = false;
      this.isAdd = true;
      this.$refs["position"].mapApi.clear();
      this.$refs["position"].mapApi.mapOper.setFullExtent();
      this.dialogForm = {
          x: "",
        y: "",
        districtCode: "",
        happenAddress: "610000",
        happenAddressDetail: ""
      };
    }
  }
};
</script>
<style>
.add-case-dialog .el-date-editor.el-input {
  width: 100%;
}
.add-case-dialog .lon-lat {
  display: inline-block;
  padding: 0 10px;
  font-size: 16px;
}
.add-case-dialog .lon-lat span {
  padding-right: 10px;
}
</style>